import "./List.css";
import React, { useState, useEffect } from "react";
import axios from "axios";

const List = ({ username, idToken, mobileMode, shortURL, setError }) => {
  const [urls, setUrls] = useState([]);
  const [refreshUrls, setRefreshUrls] = useState(false);
  useEffect(() => {
    axios
      .post("/api", { idToken, username })
      .then((res) => {
        let finalList = [];
        res.data.shortUrls.forEach(({ short, full, clicks, _id, password }) => {
          finalList.push({
            short,
            full,
            clicks,
            id: _id,
            password: password ? true : false,
          });
        });
        setUrls(finalList);
      })
      .catch((err) => {
        console.log(err);
        setError("Something went wrong");
      });
  }, [username, refreshUrls, idToken, setError]);
  useEffect(() => {
    setRefreshUrls((prev) => !prev);
  }, [shortURL]);
  const deleteUrlFromList = (id, idx) => {
    const decision = window.confirm("Confirm delete ?");
    if (decision) {
      axios
        .delete(`/api/short/${id}`, {
          data: {
            idToken,
            username,
          },
        })
        .then((res) =>
          setUrls((prev) => {
            let temp = [...prev];
            temp.splice(idx, 1);
            return temp;
          })
        )
        .catch((err) => {
          console.log(err);
          setError("Something went wrong");
        });
    }
  };
  const deleteAllUrls = () => {
    const decision = window.confirm("Confirm delete ALL?");
    if (decision) {
      axios
        .delete("/api/shortUrls", { data: { idToken, username } })
        .then(() => setUrls([]))
        .catch((err) => {
          console.log(err);
          setError("Something went wrong");
        });
    }
  };
  return (
    <div className="list-container">
      <p>Your last 5 links generated</p>
      <div className="list">
        {urls.length === 0 ? (
          <div style={{ marginTop: "1rem" }}>'No data'</div>
        ) : (
          <table>
            <tr>
              <th>Short URL</th>
              <th>FULL URL</th>
              <th>Clicks</th>
            </tr>
            {urls.map((url, idx) => (
              <tr>
                <td>
                  <a
                    className="column-1"
                    target="_blank"
                    rel="noreferrer"
                    href={window.location.href + url.short}
                    onClick={() => {
                      const temp = [...urls];
                      temp[idx] = {
                        ...temp[idx],
                        clicks: temp[idx].clicks + 1,
                      };
                      setUrls(temp);
                    }}
                  >
                    {(window.location.href + url.short).slice(
                      0,
                      mobileMode ? 35 : 50
                    ) + "..."}
                  </a>
                </td>
                <td>
                  <a
                    className="column-2"
                    target="_blank"
                    rel="noreferrer nofollow"
                    href={url.full}
                  >
                    {url.full.slice(0, mobileMode ? 35 : 50) + "..."}
                  </a>
                </td>
                <td className="td-clicks">{url.clicks}</td>
                <td onClick={() => deleteUrlFromList(url.id, idx)}>
                  <p className="td-delete">Delete</p>
                </td>
                {url.password ? <td>&#x1F512;</td> : null}
              </tr>
            ))}
          </table>
        )}
      </div>
      {urls.length > 0 ? (
        <p className="delete-all" onClick={deleteAllUrls}>
          Delete ALL links
        </p>
      ) : null}
    </div>
  );
};

export default List;
